import React from 'react';
import { Link } from 'react-router-dom';

import Container from './partials/container';

const routes = [
  { name: 'JBethuel', route: '/jbethuel' },
  { name: 'Works', route: '/works' },
  { name: 'Contact', route: '/contact' },
  { name: 'Blog', route: '/blog' },
  { name: 'Books', route: '/books' },
];

const HomeItems = () => routes.map((item, index) =>
  <Link to={item.route} style={{ textDecoration: 'none' }} key={index}>
    <h1>
      <span className="home-item">{item.name}</span>
    </h1>
  </Link>
);

export default class Home extends React.Component {
  render() {
    return (
      <Container>
        <HomeItems />
      </Container>
    );
  }
}



