import React from 'react';

import { Container, Footer } from './partials';

const Link = props => (
  <a
    className="text-white"
    style={{ textDecoration: 'underline' }}
    href={props.href}
  >
    {props.name}
  </a>
);

export default class Jbethuel extends React.Component {
  render() {
    return (
      <Container>
        <h2 className="text-white">JBethuel</h2>
        <p>Hi, I’m Bethuel.</p>
        <p>
          I'm a software developer for 2 years already. I really like learning
          different technologies especially when it comes to software
          development.
        </p>
        <p>
          I am currently working as a fullstack developer of Agila Innovations.
          Our app is <Link href="https://streetby.com" name="StreetBy" />. Which
          is a total lifestyle platform. Our vision is to be the recognized
          platform leader.
        </p>
        <p>
          It is built with{' '}
          <Link
            href="https://facebook.github.io/react-native/"
            name="React Native"
          />
          . An awesome framework/technology to develop native mobile
          application. It has currently 60+ reviews on both on Google play store
          and on Apple app store.
        </p>
        <br />
        <p>
          Feel free to explore my portfolio. I am also currently open for any
          small projects just checkout my contact page.
        </p>
        <Footer />
      </Container>
    );
  }
}



