import React from 'react';
import { Link } from 'react-router-dom';

const routes = [
  { name: 'JBethuel', route: '/jbethuel' },
  { name: 'Works', route: '/works' },
  { name: 'Contact', route: '/contact' },
  { name: 'Blog', route: '/blog' },
  { name: 'Books', route: '/books' },
  { name: 'Home', route: '/' },
];

const FooterItems = () => {
  return routes.map((item, index) => (
    <Link to={item.route} style={{ textDecoration: 'none', color: '#fff' }} key={index}>
      <span className="home-item">{item.name}</span>
      {index !== routes.length - 1 && <span>·</span>}
    </Link>
  ));
};

const Footer = () => (
  <div>
    <hr style={{ backgroundColor: '#fff' }} />
    <p>
      <FooterItems />
    </p>
  </div>
);

export default Footer;



