import React from 'react';

import Container from './partials/container';
import Footer from './partials/footer';

export default class Works extends React.Component {
  render() {
    return (
      <Container>
        <h2 className="text-white">Works</h2>
        <h4>- Student Toolkit</h4>
        <p>
          &nbsp;&nbsp; A mobile app that we developed during our
          capstone/thesis. It is a tool to view/calculate the student's grade in
          our school. It is built with Meteor JS. It is a near real-time app.
          You can check the source code on my github page.
        </p>
        <h4>- StreetBy</h4>
        <p>
          &nbsp;&nbsp; Our project(current). The total lifestyle platform app.
        </p>
        <Footer />
      </Container>
    );
  }
}



