import React from 'react';

const Container = props => (
  <div className="container">
    <div className="row" style={{ marginTop: 50 }}>
      <div className="col-sm" />
      <div className="col-sm-8">{props.children}</div>
      <div className="col-sm" />
    </div>
  </div>
);

export default Container;



